.tittle {
  color: $main-text-color;
  font-size: 48px;
  font-weight: 400;
  line-height: 85%;
  letter-spacing: -2.4px;
  text-transform: uppercase;

  @include onTablet {
    font-size: 56px;
    letter-spacing: -2.8px;
  }

  @include onSmallDesktop {
    font-size: 72px;
    letter-spacing: -3.6px;
  }
}
